*{
    box-sizing:border-box;
  }
  
  body{
    margin:0;
    padding:0;
  }
  
  .navbar{    
    display:flex;
    justify-content:space-around;
    align-items:center;
    background-color:#fff;
    color:#333;
  }
  
  .brand-title{
    font-size:1.5rem;
   
  }
  
  .navbar-links ul{
    margin-right: 0rem;
    padding:0;
    display:flex;
  }
  
  .navbar-links li{    
    list-style:none;
  }
  
  .navbar-links li a{
    text-decoration:none;
    color:#333;
    padding:1rem;
    display:block;
  }
  
  .navbar-links li:hover{
    background-color: #ffafbc;
  }
  
  .toggle-button{
    position: absolute;
    top:.75rem;
    right:1rem;
    display: none;
    flex-direction:column;
    justify-content:space-around;
    width:30px;
    height:21px;
  }
  
  .toggle-button .bar{
    height:5px;
    width:100%;
    background-color:#4E54C8;
    border-radius: 10px;
  }

.mycontainer{
  width:80%;
  height:100%;
}


.mytext{
    width:100%;       
    text-align: center;
}

.mytext h4{
  font-family: Raleway, helvetica, sans-serif;
  font-size: 26px;
  font-weight: 400; 
  margin: 5% 0% 0% 0%; 
  
}



.mytext h3{
  font-family: Raleway, helvetica, sans-serif;
  font-size: 56px;
  font-weight: 400;
  margin: 5% 0% 0% 0%; 
    
}

.mytext p{
  font-family: Raleway, helvetica, sans-serif;
  font-size: 40px;
  font-weight: 400;
  margin: 5% 0% 0% 0%; 
   
}

.shape{  
  border-radius:36% 64% 40% 60% / 74% 25% 75% 26% ;
  padding: 20px; 
  width: 400px;
  height: 400px; 
  z-index: -1;
}

.box{
  width:auto;
  height:auto;
  box-shadow: 0px 0px 5px 0px #A2A2A2;
  padding: 5px 5px 5px 5px;  
 
}

.box p{
  font-family: Raleway, helvetica, sans-serif;
  font-size: 16px;
  font-weight: 500; 
  text-align: center;
  color: #ff94a7;
}


.box .subText{
  font-size: 20px;
  font-weight: 800; 
}


.margin-top{
  margin-top: 10rem;
}
.margin-left{
  margin-left: 5%;
}

.skill-container{
width: 80%;

}

.title{
  font-size: 25px;
  font-family: Arial Rounded MT;
  font-style: normal;
  color: #707070;
  text-align: center;
}





.titleImg{  
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  width: 50px;
  height: 50px;
  border: 2px solid black;
  border-radius: 50%;
}

.buttons{
  
  width: auto;
  border-radius: 30px;
  border: 1px groove;
  padding: 5px 10px 5px 10px;
  margin: 3px 5px 5px 5px;
  color: #707070;
  background-color: white;
  font-family: Arial Rounded MT;
  font-size: 18px;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(242, 242, 242);
  padding: 12px 16px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}



  @media(max-width:770px){

    .brand-title{
     
      margin-left:2rem;
    }
    .toggle-button{
      display: flex;
    }
  
    .navbar-links{
      display:none;
      width:100%;
    }
  
    .navbar{
      flex-direction:column;
      align-items:flex-start;
    }
  
    .navbar-links ul{
      width:100%;
      flex-direction: column;
    }
  
    .navbar-links li{
      text-align:center;
    }
  
    .navbar-links li a {
      padding:.5rem 1rem;
    }
  
    .navbar-links.active{
      display: inline-block;
    }
    .mytext{
      margin-left: 5%;
  }



    
  }

  @media(max-width:990px){
    .shape{  
      margin-top: 8%;
      display:block;
      margin-left: auto;
      margin-right: auto;
     
    }
    .box{
      margin-top: 4%;     
    }

    .margin-left{
      margin: 1%;
    }
  }
   